<template>
    <div id="app" class="p-app-servers">
        <Header></Header>
        <Breadcrumb
            name="开服监控"
            slug="servers"
            root="/app/servers"
            class="m-app-servers-bread"
        >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 60" v-bind:slot="'logo'" v-bind:class="'u-app-servers'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M.835 22A4.512 4.512 0 000 24.608v11.783A4.613 4.613 0 004.608 41H29v8.101A5.016 5.016 0 0025.101 53H1a1 1 0 100 2h24.101c.465 2.279 2.484 4 4.899 4s4.434-1.721 4.899-4H59a1 1 0 100-2H34.899A5.016 5.016 0 0031 49.101V41h24.392A4.613 4.613 0 0060 36.392V24.608A4.51 4.51 0 0059.165 22H.835zM10.5 35C8.019 35 6 32.981 6 30.5S8.019 26 10.5 26s4.5 2.019 4.5 4.5-2.019 4.5-4.5 4.5zM33 54c0 1.654-1.346 3-3 3s-3-1.346-3-3 1.346-3 3-3 3 1.346 3 3zm1-24a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zM55.392 1H4.608A4.613 4.613 0 000 5.608v11.783C0 18.365.314 19.26.835 20h58.329c.522-.74.836-1.635.836-2.608V5.608A4.613 4.613 0 0055.392 1zM10.5 16C8.019 16 6 13.981 6 11.5S8.019 7 10.5 7 15 9.019 15 11.5 12.981 16 10.5 16zM34 11a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2zm2-3a1 1 0 110-2 1 1 0 010 2zm2 3a1 1 0 110-2 1 1 0 010 2z"/></svg>
            <div class="u-misc">
                常用链接 :
                <a href="http://zt.xoyo.com/jx3/server/">合服查询</a>
            </div>
        </Breadcrumb>
        <LeftSidebar>
            <Nav />
        </LeftSidebar>
        <Main :withoutRight="false">
            <div class="m-app-servers">
                <!-- 标题 -->
                <h1 class="m-app-servers-title">剑三服务器实时监控面板</h1>

                <!-- 搜索框 -->
                <div class="searchbar-wrapper">
                    <el-input
                        placeholder="搜索服务器"
                        v-model="searchServerName"
                        class="input-with-select"
                    >
                        <template slot="prepend">服务器名</template>
                        <template slot="append">
                            <el-switch
                                style="display: block"
                                v-model="isShowMainServer"
                                active-text="只看主服"
                            ></el-switch>
                        </template>
                    </el-input>
                </div>

                <!-- 收藏列表 -->
                <el-row
                    :gutter="20"
                    class="server-wrapper server-group-pinned"
                    v-show="
                        pinnedServerName.length > 0 && serverList.length > 0
                    "
                >
                    <template v-for="(server, index) in serverList">
                        <f-server-node
                            :key="index"
                            :server="server"
                            :pinned="true"
                            @toogle-server="clickServer"
                            v-show="
                                pinnedServerName.includes(server.serverName)
                            "
                        />
                    </template>
                </el-row>

                <!-- 全部列表 -->
                <el-row
                    :gutter="20"
                    class="server-wrapper server-group-unpinned"
                >
                    <template v-for="(server, index) in serverList">
                        <f-server-node
                            :key="index"
                            width="200px"
                            :server="server"
                            :pinned="false"
                            @toogle-server="clickServer"
                            v-show="showUnpinnedServerCondition(server)"
                        />
                    </template>
                </el-row>
            </div>
            <RightSidebar>
                <div class="m-app-servers-aside">
                    <Github_REPO REPO="app" coder="172,8"></Github_REPO>
                </div>
                <Extend/>
            </RightSidebar>
            <Footer></Footer>
        </Main>
    </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import FServerNode from "./FServerNode.vue";
import { axios } from "@/service/api.js";
import { JX3BOX, User } from "@jx3box/jx3box-common";
import Extend from '@/components/Extend.vue';

export default {
    name: "Servers",
    data: function() {
        return {
            searchServerName: "",
            isShowMainServer: true,
            serverList: [],
            pinnedServerName: [],
        };
    },
    computed: {},
    methods: {
        showUnpinnedServerCondition(server) {
            let searchServerNameTrimed = this.searchServerName.replace(
                /\ /g,
                ""
            );
            let isSearchInputEmpty = searchServerNameTrimed === "";
            let isServerNameSearched =
                server.serverName.indexOf(searchServerNameTrimed) !== -1;
            let isMainServer = server.serverName === server.mainServer;
            return (
                !this.pinnedServerName.includes(server.serverName) &&
                ((!isSearchInputEmpty && isServerNameSearched) ||
                    (isSearchInputEmpty && !this.isShowMainServer) ||
                    (isSearchInputEmpty && isMainServer))
            );
        },
        clickServer(serverName) {
            let index = $.inArray(serverName, this.pinnedServerName);
            if (index === -1) {
                this.pinnedServerName.push(serverName);
            } else {
                this.pinnedServerName.splice(index, 1);
            }
            this.setSavedServers();
        },
        loadAllServers() {
            // let url = 'http://localhost:3002/jx3servers';
            let url = JX3BOX.__spider + "jx3servers";
            axios(url, "GET")
                .then((response) => {
                    if (response.msg === "success") {
                        let tmpMainServerList = [];
                        let tmpNotMainServerList = response.data.filter(
                            (server) => {
                                if (server.serverName === server.mainServer) {
                                    tmpMainServerList.push(server);
                                }
                                return server.serverName !== server.mainServer;
                            }
                        );
                        this.serverList = tmpMainServerList.concat(
                            tmpNotMainServerList
                        );
                    }
                })
                .catch((e) => {
                    console.log(e);
                })
                .then(() => {});
        },
        getUserId() {
            if (User.isLogin()) {
                this.uid = User.getInfo().uid;
            }
        },
        getSavedServers() {
            // 获取用户储存的服务器列表
            let url = JX3BOX.__server + "user/meta";
            if (this.uid) {
                // 从服务器读取
                axios(
                    url,
                    "GET",
                    true,
                    {},
                    {},
                    { uid: this.uid, key: "jx3_servers" }
                )
                    .then((response) => {
                        if (response.code == 10050) {
                            let serverValue = response.data.value;
                            if (serverValue) {
                                this.pinnedServerName = serverValue.split(",");
                            } else {
                                this.pinnedServerName = [];
                            }
                        }
                    })
                    .catch((e) => {
                        switch (e.code) {
                            case -1:
                                // 网络异常
                                this.$message.error(e.msg);
                                this.getFromLocal();
                                break;
                            case 9999:
                                this.$message.error("登录失效, 请重新登录");
                                //1.注销
                                User.destroy();
                                //2.保存未提交成功的信息
                                //请保存至IndexedDB,勿占用localstorage
                                //3.跳转至登录页携带redirect
                                setTimeout(() => {
                                    User.toLogin();
                                }, 2000);
                                //不指定url时则自动跳回当前所在页面
                                break;
                            default:
                                // 服务器错误
                                this.$message.error(`[${e.code}]${e.msg}`);
                                this.getFromLocal();
                        }
                    })
                    .then(() => {});
            } else {
                // 本地读取
                this.getFromLocal();
            }
        },
        getFromLocal() {
            if (window.localStorage) {
                let current = localStorage.getItem("jx3_servers");
                if (current) {
                    this.pinnedServerName = current.split(",");
                }
            }
        },
        setSavedServers() {
            if (this.uid) {
                // 保存到服务器
                let url = JX3BOX.__server + "user/meta";
                axios(url, "POST", true, {
                    uid: this.uid,
                    key: "jx3_servers",
                    value: this.pinnedServerName.join(","),
                })
                    .then((response) => {
                        if (response.code == 10052) {
                            // 成功
                        }
                    })
                    .catch((e) => {
                        switch (e.code) {
                            case -1:
                                // 网络异常
                                this.$message.error(e.msg);
                                this.setToLocal();
                                break;
                            case 9999:
                                this.$message.error("登录失效, 请重新登录");
                                //1.注销
                                User.destroy();
                                //2.保存未提交成功的信息
                                //请保存至IndexedDB,勿占用localstorage
                                //3.跳转至登录页携带redirect
                                setTimeout(() => {
                                    User.toLogin();
                                }, 2000);
                                //不指定url时则自动跳回当前所在页面
                                break;
                            default:
                                // 服务器错误
                                this.$message.error(`[${e.code}]${e.msg}`);
                                this.setToLocal();
                        }
                    })
                    .then(() => {});
            } else {
                // 储存在本地
                this.setToLocal();
            }
        },
        setToLocal() {
            if (window.localStorage) {
                let names = this.pinnedServerName.join(",");
                localStorage.setItem("jx3_servers", names);
            }
        },
    },
    filters: {},
    mounted: function() {
        this.getUserId();
        this.getSavedServers();
        this.loadAllServers();
    },
    components: {
        Nav,
        FServerNode,
        Extend
    },
};
</script>

